import {
    CircularProgress, Container, Box
} from '@mui/material';


const LoadingScreen = ({ setAdminUserName }) => {
    return (
        <Container sx={{ mt: '40vh' }}>
            <Box display="flex" flexDirection="column" alignItems="center" width="100%">
                <CircularProgress />
            </Box>
        </Container>
    )
}

export default LoadingScreen