import {Component} from 'react';
import LoadingScreen from './components/LoadingScreen';
import Dashboard from './pages/Dashboard';
import Loginpage from './pages/Loginpage';
import axios from 'axios';
import * as urls from './data/backendUrls';

class Maincomponent extends Component {
    constructor() {
        super();
        this.state = {
            admin_username: undefined,
        }
    }
    getAdminUserName = async () => {
        try {
            let response = await axios.get(urls.getUserName);
            this.setdminUsername(response.data.username);
        } catch (error) {
            console.log(error);
        }
    }
    logoutUser = async () => {
        try {
            await axios.get(urls.logoutUrl)
        } catch (error) {
            alert(error.message);
            return;
        }
        this.setState({
            ...this.state,
            admin_username: null
        })
    }
    setdminUsername = (username) => {
        this.setState({...this.state, admin_username: username})
    }
    componentDidMount() {
        if (this.state.admin_username === undefined) {
            this.getAdminUserName();
        }
    }
    render() {
        if (this.state.admin_username === undefined) {
            return <LoadingScreen setAdminUserName={this.setdminUsername} />
        } else if (this.state.admin_username === null) {
            return <Loginpage setAdminUserName={this.setdminUsername} />
        } else {
            return <Dashboard username={this.state.admin_username} logutUser={this.logoutUser} />
        }
    }
}

export default Maincomponent