import { Component } from 'react';
import {
    Box, Container
} from '@mui/material';
import RegTable from '../components/RegTable'
import LoadingScreen from '../components/LoadingScreen';
import Navbar from '../components/Navbar';
import ActionTab from '../components/ActionTab';
import * as urls from '../data/backendUrls';
import axios from 'axios';


class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            contest: 'all',
            approved: 'all', // approved and upapproved,
            registrations: null
        }
    }
    setAction = (event) => {
        let { name, value } = event.target;
        console.log(name, value);

        this.setState(
            prevState => ({
                ...prevState,
                [name]: value
            }),
            () => this.loadRegistrations()
        )
    }
    loadRegistrations = async () => {
        try {
            let response = await axios.get(urls.registrationListUrl + `?contest=${this.state.contest}&approval=${this.state.approved}`);
            this.setState({
                ...this.state,
                registrations: response.data
            })
        } catch (error) {
            console.log(error.message);
        }
    }
    componentDidMount() {
        this.loadRegistrations();
    }
    render() {
        return (
            <Box sx={{ margin: 0, padding: 0 }}>
                <Navbar username={this.props.username} logutUser={this.props.logutUser} />
                <ActionTab
                    contest={this.state.contest}
                    approved={this.state.approved}
                    setAction={this.setAction}
                    loadRegistrations={this.loadRegistrations}
                />
                <Container sx={{ mt: 3 }}>
                    {
                        this.state.registrations == null ?
                            <LoadingScreen />
                            : <RegTable loadRegistrations={this.loadRegistrations} registrations={this.state.registrations} />
                    }
                </Container>
                
            </Box>
        )
    }
}

export default Dashboard